@import url(https://fonts.googleapis.com/css2?family=Alegreya:ital,wght@0,400;0,700;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}body{color:#62666d;position:relative;background-color:#f5f5f5;font-family:"Alegreya", serif}.flex{display:flex;grid-gap:6px;gap:6px;align-items:center}hr{margin:0 auto;width:96%;border:0;height:0;border-top:1px solid rgba(0,0,0,0.1);border-bottom:1px solid rgba(255,255,255,0.3)}.cursor{cursor:pointer}.basicPadding,.checkout,.suggetion{padding:10px}.basicPadding hr,.checkout hr,.suggetion hr{margin-top:20px}.nav{z-index:1000;position:fixed;top:0;left:0;width:100%;background-color:#fff;box-shadow:rgba(60,64,67,0.3) 0px 1px 2px 0px,rgba(60,64,67,0.15) 0px 1px 3px 1px;padding:14px}.nav .menu{position:relative;display:flex;justify-content:flex-end;max-width:90%}.nav .icon{font-size:1.8rem;cursor:pointer}.nav .amount{font-weight:500;text-align:center;position:absolute;top:-10px;right:-10px;font-size:0.99rem;width:20px;height:20px;background-color:#4b4ba5;border-radius:50%;color:white}.main{margin:0 auto;width:90vw;margin-top:120px}.main .wrapper{display:grid;grid-template-columns:75% 25%;grid-template-rows:auto;grid-gap:15px;gap:15px}.card{background-color:#fff;box-shadow:rgba(0,0,0,0.24) 0px 3px 8px;border-radius:3px}.list img{width:150px}.render{padding:30px;margin:15px;padding-bottom:0px;display:flex;grid-gap:20px;gap:20px}.render img{flex:0 1 100px}.render .description{flex:1 1}.render h3{align-self:flex-end}.render #color{display:block;width:13px;height:13px;border-radius:50%;border:1px solid #ccced8}.render .btn{height:30px;width:100px;border:1px solid #d6d3d3;border-radius:5px}.render .flex-btn{display:flex;justify-content:space-around;align-items:center;font-size:1.5rem;padding-top:5px;margin-top:2px}.render button{color:#665f5f;background-color:inherit;font-size:1.5rem;outline:none;border:none;cursor:pointer}.checkout button{box-shadow:rgba(0,0,0,0.16) 0px 3px 6px,rgba(0,0,0,0.23) 0px 3px 6px;margin-top:30px;font-size:1.5rem;background-color:#ef394e;color:#fff;outline:none;border:none;border-radius:6px;padding:10px;text-align:center;width:100%}.checkout button:hover{color:#c6e2c6}.checkout .space{justify-content:space-between}.checkout .space span{font-size:1.3rem}.suggetion{overflow:hidden;padding:10px;margin:10px}.suggetion .img{position:relative}.suggetion .img img{width:100px}.suggetion .img span{border:1px solid gray;text-align:center;height:30px;width:30px;font-size:1.4rem;border-radius:50%;background-color:#d9e6e9;position:absolute;top:60px;left:0;color:red}.emptycart{font-size:2.4rem;padding:90px 0;text-align:center;justify-content:center}@media only screen and (max-width: 860px){body{background-color:#f9fdff}.main .wrapper{grid-template-columns:100%;grid-template-rows:auto}.render{padding:3px;margin:5px;padding-bottom:0px}.render img{width:150px;height:150px}}@media only screen and (max-width: 660px){.main{width:100vw}.render{flex-direction:column;align-items:center}.render h3{align-self:center}}

